import './LoginEntranceFormFieldGroup.css';
import '@material/theme/dist/mdc.theme.css';
import '@material/typography/dist/mdc.typography.css';
import '@material/card/dist/mdc.card.css';
import '@material/textfield/dist/mdc.textfield.css';
import '@material/notched-outline/dist/mdc.notched-outline.css';
import '@material/line-ripple/dist/mdc.line-ripple.css';
import '@material/floating-label/dist/mdc.floating-label.css';
import '@material/button/dist/mdc.button.css';
import { TextField } from '@rmwc/textfield';
import { ThemeProvider } from '@rmwc/theme';
import { Typography } from '@rmwc/typography';
import React, { useState } from 'react';
// MWCのバグが、以下2つあるため一時的に指定している
// TextFieldの色はprimary値しか受け付けない / ラベルの色はthemaやclassを使っても、強制的に#6200eeの値になる
// TODO(hkt): RMWC側でバグが対応され次第削除する
const TEXTFIELD_THEME_OPTIONS = {
    primary: '#6200ee',
};
const ROOMID_CHARACTER_LIMIT = {
    upper: 255,
};
const USERNAME_CHARACTER_LIMIT = {
    upper: 10,
};
const validateIDString = (idString) => {
    const pattern = /^[a-zA-Z0-9.%+^_"`{|}~<>\\-]{1,255}$/;
    return idString.match(pattern) !== null;
};
// username / roomId のバリデーション
// TODO(kdxu): ログイン時のエラー仕様が決定したら、それを踏襲してこのロジックを修正する
const validateRoomIdAndUsername = (roomId, username) => {
    const errors = [];
    if (username === '') {
        errors.push({ key: 'username', message: 'ユーザ名を入力してください' });
    }
    if (username.length > USERNAME_CHARACTER_LIMIT.upper) {
        errors.push({ key: 'username', message: `ユーザ名は${USERNAME_CHARACTER_LIMIT.upper}文字以下で入力してください` });
    }
    if (roomId === '') {
        errors.push({ key: 'roomId', message: 'ルーム名を入力してください' });
    }
    if (roomId.length > ROOMID_CHARACTER_LIMIT.upper) {
        errors.push({ key: 'roomId', message: `ルーム名は${ROOMID_CHARACTER_LIMIT.upper}文字以下で入力してください` });
    }
    if (!validateIDString(roomId)) {
        errors.push({ key: 'roomId', message: '英数字と一部記号（.%+^_"`{|}~<>\\-）以外の文字列は使用できません' });
    }
    return errors;
};
const LoginEntranceInputItem = (props) => {
    const error = props.validateErrors.find((v) => v.key === props.name);
    const helpText = error && {
        persistent: true,
        validationMsg: true,
        children: error.message,
    };
    const isInvalid = error !== undefined;
    const title = `${props.label}を入力してください`;
    return (React.createElement(React.Fragment, null,
        React.createElement(Typography, { use: "body1", tag: "p", theme: "textSecondaryOnBackground" }, title),
        React.createElement(ThemeProvider, { options: TEXTFIELD_THEME_OPTIONS },
            React.createElement(TextField, { onChange: props.onChange, style: { width: '100%' }, outlined: true, invalid: isInvalid, label: props.label, name: props.name, value: props.value, helpText: helpText }))));
};
const LoginEntranceFormFieldGroup = (props) => {
    const [validateErrors, setValidateResult] = useState([]);
    const handleSubmit = (e) => {
        e.preventDefault();
        const errors = validateRoomIdAndUsername(props.roomId, props.username);
        setValidateResult(errors);
        if (errors.length > 0) {
            return;
        }
        // validation エラーが無い場合ログイン処理を行う
        props.onSubmitSuccess();
    };
    const handleChangeRoomId = (e) => {
        e.preventDefault();
        props.onChangeRoomId(e.target.value);
    };
    const handleChangeUsername = (e) => {
        e.preventDefault();
        props.onChangeUsername(e.target.value);
    };
    return (React.createElement("div", { className: "mdc-card" },
        React.createElement("div", { className: "join-content" },
            React.createElement(Typography, { use: "headline6", tag: "h1", theme: "onSurface" }, "RICOH Live Streaming Service"),
            React.createElement("form", null,
                React.createElement("div", { className: "form-group" },
                    React.createElement(LoginEntranceInputItem, { onChange: handleChangeRoomId, label: "\u30EB\u30FC\u30E0\u540D", name: "roomId", value: props.roomId, validateErrors: validateErrors }),
                    React.createElement(LoginEntranceInputItem, { onChange: handleChangeUsername, label: "\u30E6\u30FC\u30B6\u540D", name: "username", value: props.username, validateErrors: validateErrors })),
                React.createElement("button", { id: "joinButton", onClick: handleSubmit, className: "mdc-button mdc-button--raised mdco-fullwidth" },
                    React.createElement("div", { className: "mdc-button__ripple" }),
                    React.createElement("span", { className: "mdc-button__label" }, "\u53C2\u52A0"))))));
};
export default LoginEntranceFormFieldGroup;
