import './Login.css';
import React, { useState } from 'react';
import LoginEntranceFormFieldGroup from '@/components/LoginEntranceFormFieldGroup';
const Login = () => {
    const [roomId, setRoomId] = useState('');
    const [username, setUsername] = useState('');
    const onSubmitSuccess = () => {
        const encodedUsername = encodeURIComponent(username);
        window.open(`/room/${roomId}/?username=${encodedUsername}`);
    };
    return (React.createElement("div", { className: "start-layout" },
        React.createElement(LoginEntranceFormFieldGroup, { username: username, roomId: roomId, onChangeRoomId: setRoomId, onChangeUsername: setUsername, onSubmitSuccess: onSubmitSuccess })));
};
export default Login;
